<template>
  <div>
    <slider v-if="!isMobile()" class="d-none d-sm-block"></slider>
    <landing></landing>
  </div>
</template>
<script>
  import axios from 'axios'
import {IP_STACK, CLIENT_VISITOR} from '../../api_routes'
import {AUTH_CONFIG} from '../../auth_config'
  const Slider = () => import('./../layout/Slider.vue');
  const Landing = () => import('./../Landing/Landing.vue');

  export default {
    name: 'Home',
    components: {Slider, Landing},
    created () {
      this.getClientIp()
    },
    methods: {
      isMobile () {
        return window.innerWidth < 767
      },
      getClientIp () {
        axios.get(IP_STACK).then(response => {
          if (response.data.ip != null) {
            let postData = {
              ip: response.data.ip,
              location: response.data.location.capital
            };
            axios.post(CLIENT_VISITOR, postData, {headers: AUTH_CONFIG.getHeaders()}).then(response => {
              console.log(response)
            })
          }
        })
      }
    }
  }
  </script>

